import { StrictMode } from "react";
import ReactDOM from "react-dom";

import App from "./App";
import ComponentTest from "./ComponentTest";
import ComponentList from "./ComponentList";

const rootElement = document.getElementById("root");

let count = 0;
setInterval(() => {
  ReactDOM.render(
    <StrictMode>
      <App count={count++} />
      <ComponentTest />
      <ComponentList />
    </StrictMode>,
    rootElement
  );
}, 1000);
